
"use client";

import { useState } from "react";
import { vehicles as initialVehicles } from "@/lib/data";
import { DataTable } from "@/components/data-table/data-table";
import { columns } from "./columns";
import { PageHeader } from "@/components/page-header";
import { Button } from "@/components/ui/button";
import { PlusCircle } from "lucide-react";
import { CreateVehicleDialog } from "@/components/create-vehicle-dialog";
import type { Vehicle } from "@/lib/types";
import { FleetDataTableToolbar } from "./toolbar";

export default function FleetPage() {
  const [vehicles, setVehicles] = useState<Vehicle[]>(initialVehicles);
  const [isDialogOpen, setIsDialogOpen] = useState(false);
  const [selectedVehicle, setSelectedVehicle] = useState<Vehicle | null>(null);

  const handleCreateNew = () => {
    setSelectedVehicle(null);
    setIsDialogOpen(true);
  };

  const handleEdit = (vehicle: Vehicle) => {
    setSelectedVehicle(vehicle);
    setIsDialogOpen(true);
  };

  const handleDelete = (vehicleId: string) => {
    if (!window.confirm("Are you sure you want to delete this vehicle? This action cannot be undone.")) {
      return;
    }
    setVehicles(prev => prev.filter(v => v.id !== vehicleId));
    // In a real app, you would also call an API to delete from the database
  };

  const handleDialogSubmit = (vehicleData: Vehicle) => {
    if (selectedVehicle) {
      // Update
      setVehicles(prev => prev.map(v => (v.id === selectedVehicle.id ? vehicleData : v)));
    } else {
      // Create
      setVehicles(prev => [vehicleData, ...prev]);
    }
    setSelectedVehicle(null);
  };

  return (
    <div>
      <PageHeader 
        title="Fleet Management" 
        description="Oversee and manage all vehicles in the fleet."
        action={
          <Button onClick={handleCreateNew}>
            <PlusCircle className="mr-2 h-4 w-4" />
            Add Vehicle
          </Button>
        }
      />
      <DataTable 
        columns={columns({ onEdit: handleEdit, onDelete: handleDelete })} 
        data={vehicles}
        toolbar={FleetDataTableToolbar}
      />
      <CreateVehicleDialog
        isOpen={isDialogOpen}
        onOpenChange={setIsDialogOpen}
        onVehicleSubmit={handleDialogSubmit}
        vehicle={selectedVehicle}
      />
    </div>
  );
}
